<template>
<section id="rsvp">
  <div id="title">
    <h2>RSVP</h2>
  </div>
  <div id="form-container">
    <div class="form-field">
      <label>Your name</label>
      <input type="name"></input>
    </div>
    <div class="form-field">
      <label>Extra people?</label>
      <input type="number"></input>
    </div>
  </div>
</section>
</template>

<script>
export default {
  name: 'TheRsvp',
  data () {
    return {
      msg: 'What'
    }
  }
}
</script>

<style scoped>
#form-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
}

.form-field {
  text-align: left;
}

.form-field label {
  display: block;
}
</style>



