}
.title-text {
  text-align: center;
  font-family: 'Work Sans';
  font-size: 1.3em;
}

#down-arrow {
  align-self: center;
  animation: arrow 2s infinite;
}

#down-arrow a {
  font-size: 2em;
  text-decoration: none;
}

@keyframes arrow {
  0% {transform: translateY(0);}
  40% {transform: translateY(25%);}
  80% {transform: translateY(0);}
  100% {transform: translateY(0);}
}

</style>



